import * as React from 'react';
import Grid from '@mui/material/Unstable_Grid2';
import TextField from '@mui/material/TextField';
import InputAdornment from '@mui/material/InputAdornment';

import {ImgIcon} from './Img';

export default function GridMRnk(props) {
  const { data, C, Calc } = props.props

  const on = (event) => {
    data.MRnk[event.target.id] = event.target.valueAsNumber
    Calc()
  }
  //columns={16}
  return (
    <Grid container spacing={1} >
      {Object.keys(data.MRnk).map((Gr, i) => {
        return (
          <React.Fragment key={i}>
            <Grid xs={4}>
              <TextField
                label={Gr}
                id={Gr}
                type="number"
                defaultValue={data.MRnk[Gr]}
                // size="small"
                variant="standard"
                onChange={on}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <ImgIcon name={Gr} width={35} />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid xs={8}>
            </Grid>
          </React.Fragment>
        )
      })}
    </Grid>
  );
}