import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';


const columns = [
  // {
  //   field: 'No',
  //   headerName: '',
  //   width: 50,
  // },

  {
    field: 'St',
    headerName: '',
    width: 100,
    sortable: false,//false:ソート不可
    disableColumnMenu: true,//メニュー不可
  },
  {
    field: 'Bas',
    headerName: '装備前',
    width: 100,
    sortable: false,//false:ソート不可
    disableColumnMenu: true,//メニュー不可
  },
  {
    field: 'Org',
    headerName: '装備後',
    width: 100,
    sortable: false,//false:ソート不可
    disableColumnMenu: true,//メニュー不可
  },
  {
    field: 'Btl',
    headerName: '戦闘時',
    width: 100,
    sortable: false,//false:ソート不可
    disableColumnMenu: true,//メニュー不可
  },
];


export default function DataGridSt(props) {
  const { data, Calc, list, cNameF, AE } = props.props

  const rows = data.HTADSIM.map((S, i) => {
    return ({
      id: i,
      St: S,
      Bas: data.C1[cNameF][AE].St.Bas[S],
      Org: data.C1[cNameF][AE].St.Org[S],
      Btl: data.C1[cNameF][AE].St.Btl[S],
    })
  })





  return (
    <Box sx={{ width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        autoHeight//行に合わせた高さ
        hideFooter//フッター非表示
        hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
        getRowHeight={() => 'auto'}
      />
    </Box>
  );
}