import * as React from 'react';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

export function TextCNameF(props) {
  const { text } = props
  return (
    <>
      <Typography
        style={{
          fontSize: 16,
          fontWeight: 'bold',
          color: 'gray'
        }}>
        {text.split('】')[0] + '】'}
      </Typography>
      <Typography
        style={{
          fontSize: 24,
          fontWeight: 'bold',
          color: 'gray'
        }}>
        {'　' + text.split('】')[1]}
      </Typography>
    </>
  )
}

export function TextRs(props) {
  const { V } = props
  return (
    <>{
      V >= 100 ? <span style={{ fontWeight: 'bold' }}> 無効</span> :
        V >= 51 ? <span style={{ fontWeight: 'bold', color: 'darkblue' }}> 激減</span> :
          V >= 26 ? <span style={{ fontWeight: 'bold', color: 'blue' }}> 半減</span> :
            V >= 1 ? <span style={{ fontWeight: 'bold', color: 'cornflowerblue' }}> 軽減</span> :
              V === 0 ? <span style={{ fontWeight: 'bold', color: 'gray' }}> 普通</span> :
                V >= -25 ? <span style={{ fontWeight: 'bold', color: 'salmon' }}> 弱点</span> :
                  V >= -50 ? <span style={{ fontWeight: 'bold', color: 'red' }}> 大弱点</span> :
                    <span style={{ fontWeight: 'bold', color: 'darkred' }}> 超弱点</span>
    }</>
  )
}

export function TextSrs(props) {
  const { name, data, Eq } = props
  console.log(props)
  let Srs = data[Eq][name].Srs
  if (data[Eq][name].Srs) {
    Srs += '(1)' + data.Srs[data[Eq][name].Srs + '1'].SrsEffList
    if (data.Srs[data[Eq][name].Srs + '2']) {
      Srs += ',(2)' + data.Srs[data[Eq][name].Srs + '2'].SrsEffList
    }
  }
  return Srs
}






export function TextBr(props) {
  // console.log(props.text.split(/(\n)/))
  return (
    props.text.split(/(\n)/).map((text, index) => {
      // console.log(text.match(/\n/))

      return (
        <React.Fragment key={index}>
          {text.match(/\n/) ? <br /> : text}
        </React.Fragment>
      )
    })
  );
}






