import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';

import {ImgIcon} from './Img';
import StyleCommon from './StyleCommon';

import RatingBff from "./RatingBff";

const WeArBdToTxt = { Wepn: '武器', Armr: '防具', Bddy: 'Buddy' }



export default function DataGridEq(props) {
  const { data, C, Calc, list, Op } = props.props

  const columns = [
    // { field: 'id', headerName: 'ID', width: 90 },
    {
      field: 'Ctg',
      headerName: '分類',
      width: 63,
    },
    {
      field: 'Nam',
      headerName: '名称',
      width: 120,
    },
    {
      field: 'Typ',
      headerName: '攻撃/妨害/補助',
      minWidth: 49,
      width: 49,
      renderHeader: (params) => (''),
    },
    {
      field: 'Knd',
      headerName: '物理/体技/波動/魔法',
      minWidth: 49,
      width: 49,
      renderHeader: (params) => (''),

    },
    {
      field: 'TP',
      headerName: 'TP',
      headerAlign:'right',
      align: 'right',
      minWidth: 45,
      width: 45,
    },
    {
      field: 'Elm',
      headerName: '属性',
      minWidth: 40,
      width: 40,
      align: 'center',
      renderHeader: (params) => (''),
      renderCell: (params) => (
        <ImgIcon name={params.row.Elm} width={35} />//height={35}
      )
    },
    {
      field: 'Ara',
      headerName: '射程範囲',
      headerAlign: 'center',
      width: 70,
      align: 'center',
      renderHeader: (params) => (''),

      renderCell: (params) => (
        <ImgIcon name={params.row.Ara} height={35} />//height={35}
      )
    },
    {
      field: 'Txt',
      headerName: '効果テキスト',
      width: 300,
      renderHeader: (params) => (''),

    },
    {
      field: 'UAra',
      headerName: 'ユニオン範囲',
      minWidth: 35,
      width: 35,
      align: 'center',
      renderHeader: (params) => (''),

      renderCell: (params) => (
        <ImgIcon name={params.row.UAra} height={35} />//height={35}
      )

    },
    {
      field: 'UTxt',
      headerName: 'ユニオン効果テキスト',
      width: 300,
      renderHeader: (params) => ('+UNION'),

    },
    {
      field: 'Eff',
      headerName: 'バフ・デバフ',
      width: 500,
      renderCell: (params) => (
        <>
          {params.row.Eff.map((EffEl, i) => {
            return (
              <React.Fragment key={i}>
                {EffEl.E}
                <RatingBff props={{
                  ...props.props,
                  EffName: params.row.Nam,
                  EffEl: EffEl,
                }}
                />
              </React.Fragment>
            )
          })}
        </>
      )
    },
  ];





  const rows = Object.keys(data.Skl).map((name, i) => {
    if (!data.Skl[name].Eff) data.Skl[name].Eff = []
    return ({
      ...data.Skl[name],
      id: name,
      Ctg: 'スキル',
      Nam: name,
    })
  })

  Object.keys(data.Ab).forEach((name, i) => {
    if (!data.Ab[name].Eff) data.Ab[name].Eff = []
    // console.log(data.Ab[name].Eff)
    rows.push({
      ...data.Ab[name],
      id: name,
      Ctg: '特性',
      Nam: name,
    })
  })



  return (
    // <Box sx={{ width: '100%' }}>
    <>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        autoHeight//行に合わせた高さ
        components={{
          Toolbar: GridToolbar,// ツールバーを指定する
        }}
        // hideFooter//フッター非表示
        // hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        // hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
        //   initialState={{
        //   pagination: {
        //     paginationModel: {
        //       pageSize: 5,
        //     },
        //   },
        // }}
        pageSizeOptions={[5]}
        // checkboxSelection
        // disableRowSelectionOnClick
        sx={StyleCommon}
        getRowClassName={(params) => params.row.Elm}

        getRowHeight={() => 'auto'}

      />


    </>
    // </Box>
  );
}