import * as React from 'react';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';




export default function InputZHP(props) {
  const { data, Calc } = props.props

  const onR = (event) => {
    // console.log(event.target.valueAsNumber)

    Object.keys(data.C1).forEach((cNameF) => {
      data.C1[cNameF].Ally.ZHP.R = event.target.valueAsNumber
    })

    Calc()

  }




  const [ZHP, setZHP] = React.useState(data.ZHP);

  const handleChange = (event, newZHP) => {
    setZHP(newZHP);
    if (newZHP !== null) data.ZHP = newZHP
    Calc()
  };

  const children = [
    <ToggleButton value={0} key="left">
      残りHP0%
    </ToggleButton>,
    <ToggleButton value={100} key="center">
      残りHP100%
    </ToggleButton>,
  ];

  const control = {
    value: ZHP,
    onChange: handleChange,
    exclusive: true,
  };

  return (
    <>
      <ToggleButtonGroup size="small" {...control} aria-label="Small sizes">
        {children}
      </ToggleButtonGroup>
      {/* {ZHP === '割合' &&
        <TextField
          label="残りHP割合[%]"
          id="割合"
          type="number"
          defaultValue="100"
          size="small"
          onChange={onR}
        />
      }
      {ZHP === '実数' &&
        <TextField
          label="残りHP"
          id="実数"
          type="number"
          defaultValue="1"
          size="small"
        />
      } */}




    </>
  );
}