import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';

import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Unstable_Grid2';

import DataGridChrSkl from "./DataGridChrSkl";
import DataGridDmgChr from "./DataGridDmgChr";
import DataGridBff from "./DataGridBff";
import TabParams from "./TabParams";
import RatingAwk from "./RatingAwk";
import ModalEqSlct from "./ModalEqSlct";
import Img from './Img';
import CNSplit from "./CNSplit"


import GridChr from "./GridChr";
import GridChrSkl from "./GridChrSkl";
import DetailSkill from "./DetailSkill";
import { alignProperty } from '@mui/material/styles/cssUtils';


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 1500,
  bgcolor: '#fffef2',
  border: '2px solid #000',
  boxShadow: 24,
  p: 2,
};

export default function ModalChrSkl(props) {
  const { data, Calc, list, cNameF, id, V } = props.props

  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);


  return (
    <>
      <Button onClick={handleOpen} sx={{ align: "right" }}>
        スキル・アビリティ詳細
        {/* <Img name={cNameF + 'i'} width={80} /> */}


      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          {cNameF}
          <DataGridChrSkl props={props.props}/>
        </Box>
      </Modal>
    </>
  );
}