import * as React from 'react';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';
import Autocomplete from '@mui/material/Autocomplete';
import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';

const SzList = [
  { label: '1x1', value: '1x1' },
  { label: '2x2', value: '2x2' },
  { label: '3x3', value: '3x3' },
  { label: '4x4', value: '4x4' },
  { label: '5x5', value: '5x5' },
];



export default function InputEnmy(props) {
  const { data, Calc, setSlEnmy } = props.props

  // console.log(Object.keys(data.Enmy.カスタム設定.St.Bas))

  return (
    <Box sx={{ bgcolor: '#eee' }}>
      {/* <Button
        name='カスタム設定'
        size="small"


        onClick={(event) => {
          console.log('敵を' + event.target.name + 'に変更')

          data.SlEnmy = event.target.name
          setSlEnmy(event.target.name)

          Calc()
        }}
      >
        カスタム設定
      </Button> */}
      カスタム設定
      <Grid container spacing={0.5} sx={{ p: 0.5 }} columns={15}>
        <Grid xs={1} textAlign='right' sx={{ pr: 1 }}>
          ステータス
        </Grid>
        {data.list.HADSIM.map((S, i) => {
          return (
            <Grid xs={1} key={i}>
              <TextField
                label={S}
                id={S}
                type="number"
                value={data.Enmy.カスタム設定.St.Bas[S]}
                size="small"
                fullWidth
                onChange={(event) => {
                  // console.log(event.target.id)
                  // console.log(event.target.valueAsNumber)
                  data.Enmy.カスタム設定.St.Bas[event.target.id] = event.target.valueAsNumber
                  console.log(data.Enmy.カスタム設定)
                  Calc()
                }} />
            </Grid>
          )
        })}
        <Grid xs={1}>
          <TextField
            select
            label="サイズ"
            value={data.Enmy.カスタム設定.Sz}
            name='Sz'
            size="small"
            fullWidth
            onChange={(event) => {
              // console.log(event.target)
              data.Enmy.カスタム設定.Sz = event.target.value
              console.log(data.Enmy.カスタム設定)
              Calc()
            }}
          >
            {SzList.map((el) => (
              <MenuItem key={el.value} value={el.value}>
                {el.label}
              </MenuItem>
            ))}
          </TextField>
        </Grid>

      </Grid>
      <Grid container spacing={0.5} sx={{ p: 0.5 }} columns={15}>
        <Grid xs={1} textAlign='right' sx={{ pr: 1 }}>
          属性耐性
        </Grid>
        {data.list.属性耐性.map((RsNam, i) => {
          return (
            <Grid xs={1} key={i}>
              <TextField
                id={RsNam}
                select
                label={RsNam}
                value={
                  data.Enmy.カスタム設定.Rs[RsNam.split('属性')[0]] === 0 ? '普通' :
                    data.Enmy.カスタム設定.Rs[RsNam.split('属性')[0]] === 50 ? '半減' :
                      data.Enmy.カスタム設定.Rs[RsNam.split('属性')[0]] === 100 ? '無効' : '大弱点'
                }
                name={RsNam}
                size="small"
                fullWidth
                onChange={(event) => {
                  // console.log(event.target)
                  data.Enmy.カスタム設定.Rs[event.target.name.split('属性')[0]] = data.RsTxtToV[event.target.value]
                  console.log(data.Enmy.カスタム設定)
                  Calc()
                }}
              >
                {RsList.map((el) => (
                  <MenuItem key={el.value} value={el.value}>
                    {el.label}
                  </MenuItem>
                ))}
              </TextField>
            </Grid>
          )
        })
        }
      </Grid>


    </Box>
  );
}

const RsList = [
  { label: '大弱点', value: '大弱点' },
  { label: '普通', value: '普通' },
  { label: '半減', value: '半減' },
  // { label: '無効', value: '無効' },
];
