import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import Button from '@mui/material/Button';

import ModalEqSlct from "./ModalEqSlct";
import ModalOpSlct from "./ModalOpSlct"
import CNSplit from "./CNSplit"
import { TextSrs } from './Text';



const WeArBdToTxt = { Wepn: '武器', Armr: '防具', Bddy: 'Buddy' }



export default function DataGridEq(props) {
  const { data, C, cNameF, Calc, setEqs } = props.props

  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)

  const columns = [
    {
      field: 'name',
      headerName: '',
      width: 200,
      renderCell: (params) => (
        <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: params.row.Eq }} />
      )
    },
    {
      field: 'UqOp',
      headerName: '固有特性',
      width: 250,
    },
    {
      field: 'Op',
      headerName: '特性(x3)',
      width: 250,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          Eq={params.row.Eq}
          OpB={params.row.Op}
          data={data}
          Calc={Calc}
        />
      )

    },

  ];

  const EqObj = {
    Wepn: data.C1[cNameF].Ally.Wepn,
    Armr: data.C1[cNameF].Ally.Armr,
    Bddy: data.C1[cNameF].Ally.Bddy,
  }


  const rows = Object.keys(EqObj).map((Eq, i) => {
    return ({
      id: EqObj[Eq],
      name: EqObj[Eq],
      Eq: Eq,
      UqOp: data[Eq][EqObj[Eq]].UqOp,
      Op: data[Eq][EqObj[Eq]].Op,
    })
  })

  return (
    <Box sx={{ height: 150, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        autoHeight//行に合わせた高さ
        hideFooter//フッター非表示
        hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
        getRowHeight={() => 'auto'}
      />
    </Box>
  );
}