import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import Button from '@mui/material/Button';

import ModalCommon from './ModalCommon';
import { ImgIcon, ImgDmg } from './Img';
import StyleCommon from './StyleCommon';
import CNSplit from "./CNSplit"


export default function DataGridDmg(props) {
  // const DataGridDmg = React.memo((props) => {
  // // const DataGridDmg = (props) => {
  const { data, Calc, setLS } = props.props

  const columns = [
    {
      field: 'Rr',
      headerName: 'レア',
      minWidth: 40,
      width: 40,
      align: 'center',
      disableColumnMenu: true,//メニュー不可
      renderHeader: (params) => (''),

    },
    {
      field: 'name',
      headerName: 'キャラクター',
      width: 80,
      align: 'center',
      renderHeader: (params) => (''),

      renderCell: (params) => (
        <ModalCommon props={{ ...props.props, cNameF: params.row.name, id: 'キャラ' }} C={props.C} />
      )
    },
    {
      field: 'Gr',
      headerName: 'グループ',
      minWidth: 40,
      width: 40,
      align: 'center',
      renderHeader: (params) => (''),


      renderCell: (params) => (
        <ImgIcon name={params.row.Gr} width={35} />
      )
    },
    {
      field: 'Typ',
      headerName: 'タイプ',
      minWidth: 40,
      width: 40,
      align: 'center',
      renderHeader: (params) => (''),

      renderCell: (params) => (
        <ImgIcon name={params.row.Typ} width={35} />
      )
    },
    // {
    //   field: 'HP',
    //   headerName: 'HP',
    //   align: 'right',
    //   width: 55,
    // },
    // {
    //   field: 'TP',
    //   headerName: 'TP',
    //   align: 'right',
    //   width: 55,
    // },
    // {
    //   field: '攻撃力',
    //   headerName: '攻撃力',
    //   align: 'right',
    //   width: 55,
    // },
    // {
    //   field: 'D',
    //   headerName: '防御力',
    //   align: 'right',
    //   width: 55,
    // },
    {
      field: '速力',
      headerName: '速力',
      minWidth: 49,
      width: 49,
      align: 'right',
      disableColumnMenu: true,//メニュー不可

    },
    // {
    //   field: 'I',
    //   headerName: '精神力',
    //   align: 'right',
    //   width: 55,
    // },
    {
      field: '移動力',
      headerName: '移動',
      align: 'center',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可

    },
    {
      field: 'LS',
      headerName: 'リーダースキル',
      width: 200,
      renderCell: (params) => (
        <Button
          variant="text"
          size='small'
          // name={params.row.LS}
          onClick={(event, newValue) => {
            // console.log(event.target.innerText)
            data.LS = event.target.innerText
            setLS(event.target.innerText)

            // data.C1[cNameF].Ally.Awk = newValue
            // setValue(newValue);
            Calc()
          }}


        >{params.row.LS}</Button>
      )

    },

    // {
    //   field: 'Skl1',
    //   headerName: 'スキル1',
    //   width: 100,
    // },
    // {
    //   field: 'Skl2',
    //   headerName: 'スキル2',
    //   width: 100,
    // },
    // {
    //   field: 'Skl3',
    //   headerName: 'スキル3',
    //   width: 100,
    // },

    // {
    //   field: 'Dmg_1',
    //   headerName: 'スキル1',
    //   headerAlign: 'center',
    //   headerClassName: 'S1',
    //   align: 'right',
    //   width: 120,
    //   renderCell: (params) => params.row.DmgTxt1
    // },
    {
      field: 'Ara_1',
      headerName: 'スキル1',
      headerAlign: 'center',
      headerClassName: 'S1',

      align: 'center',
      width: 72,
      sortable: false,//false:ソート不可
      disableColumnMenu: true,//メニュー不可


      renderCell: (params) => (
        <ImgIcon name={params.row.Ara_1} width={72} />
      )
    },
    {
      field: 'DmgBar_1',
      headerName: '与ダメージ',
      headerClassName: 'S1',
      width: 100,
      disableColumnMenu: true,//メニュー不可
      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg_1}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      )
    },

    // {
    //   field: 'Dmg_2',
    //   headerName: 'スキル2',
    //   headerAlign: 'center',
    //   headerClassName: 'S2',
    //   align: 'right',
    //   width: 120,
    //   renderCell: (params) => params.row.DmgTxt2

    // renderCell: (params) => (
    //   <ModalCommon
    //     cNameF={params.row.name}
    //     C={props.C}
    //     data={props.data}
    //     list={list}
    //     Calc={Calc}
    //     id={'スキル2'}
    //     V={params.row.Dmg_2D}
    //   />
    // )

    // },
    {
      field: 'Ara_2',
      headerName: 'スキル2',
      headerAlign: 'center',
      headerClassName: 'S2',
      align: 'center',
      width: 72,
      sortable: false,//false:ソート不可
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => (
        <ImgIcon name={params.row.Ara_2} height={35} />
      )
    },
    {
      field: 'DmgBar_2',
      headerName: '与ダメージ',
      headerClassName: 'S2',

      width: 100,
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg_2}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      )
    },


    // {
    //   field: 'Dmg_3',
    //   headerName: 'スキル3',
    //   headerClassName: 'S3',
    //   headerAlign: 'center',
    //   width: 120,
    //   renderCell: (params) => params.row.DmgTxt3
    // },
    {
      field: 'Ara_3',
      headerName: 'スキル3',
      headerAlign: 'center',
      headerClassName: 'S3',

      align: 'center',
      width: 72,
      sortable: false,//false:ソート不可
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => (
        <ImgIcon name={params.row.Ara_3} height={35} />
      )
    },

    {
      field: 'DmgBar_3',
      headerName: '与ダメージ',
      headerClassName: 'S3',

      width: 100,
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg_3}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      )
    },
    {
      field: 'DmgMax',
      headerName: '与ダメMAX',
      width: 100,
      disableColumnMenu: true,//メニュー不可

      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.DmgMax}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      ),
      description: '所持スキル(継承スキル含む)での与ダメージの最大',
    },
    {
      field: 'HDmgMax',
      headerName: '被ダメMAX',
      width: 100,
      disableColumnMenu: true,//メニュー不可
      renderCell: (params) => (
        <ImgDmg 
        data={data}
        Dmg={params.row.HDmgMax}
        HP={params.row.HP}
        AE='Enmy'
      />
      ),
      description: '敵所持スキルの中での被ダメージの最大(緑色は残りHP)',
    },
    {
      field: 'HDmgMaxRt',
      headerName: '被ダメMAX割合',
      width: 100,
      disableColumnMenu: true,//メニュー不可

      // valueFormatter: (value) => `${value}% `,
      renderHeader: (params) => (
        '←割合'
      ),

      renderCell: (params) => (
        <>{params.row.HDmgMaxRt + '%'}</>
      ),
      description: '敵所持スキルの中での最大被ダメージの割合',
    },



  ];





  const rows = []

  Object.keys(data.C1).forEach((cNameF, i) => {

    let cName1 = CNSplit(cNameF, 1)
    let cName2 = CNSplit(cNameF, 2)




    let DmgMax = 0
    data.C1[cNameF].Ally.Skl2.forEach((elSkl) => {
      DmgMax = Math.max(DmgMax, elSkl.Dmg)
    })

    let HDmgMax = 0
    data.C1[cNameF].Enmy.Skl2.forEach((elSkl) => {
      HDmgMax = Math.max(HDmgMax, elSkl.Dmg)
    })
    let HDmgMaxRt = Math.round(HDmgMax / data.C1[cNameF].Ally.St.Btl.HP * 100)


    rows.push({
      ...data.Chr[cName1][cName2],
      ...data.C1[cNameF].Ally.St.Btl,
      id: cNameF,
      name: cNameF,
      Skl1: data.Chr[cName1][cName2].Skl[0],
      Skl2: data.Chr[cName1][cName2].Skl[1],
      Skl3: data.Chr[cName1][cName2].Skl[2],
      Dmg_1: data.C1[cNameF].Ally.Skl2[0].Dmg,
      Dmg_2: data.C1[cNameF].Ally.Skl2[1].Dmg,
      Dmg_3: data.C1[cNameF].Ally.Skl2[2].Dmg,
      DmgTxt1: data.C1[cNameF].Ally.Skl2[0].DmgTxt,
      DmgTxt2: data.C1[cNameF].Ally.Skl2[1].DmgTxt,
      DmgTxt3: data.C1[cNameF].Ally.Skl2[2].DmgTxt,
      DmgBar_1: data.C1[cNameF].Ally.Skl2[0].Dmg,
      DmgBar_2: data.C1[cNameF].Ally.Skl2[1].Dmg,
      DmgBar_3: data.C1[cNameF].Ally.Skl2[2].Dmg,
      Ara_1: data.Skl[data.Chr[cName1][cName2].Skl[0]].Ara,
      Ara_2: data.Skl[data.Chr[cName1][cName2].Skl[1]].Ara,
      Ara_3: data.Skl[data.Chr[cName1][cName2].Skl[2]].Ara,
      Elm1: data.Skl[data.Chr[cName1][cName2].Skl[0]].Elm,
      Elm2: data.Skl[data.Chr[cName1][cName2].Skl[1]].Elm,
      Elm3: data.Skl[data.Chr[cName1][cName2].Skl[2]].Elm,
      DmgMax: DmgMax,
      HDmgMax: HDmgMax,
      HDmgMaxRt: HDmgMaxRt,
    })
  })

  return (
    // <Box sx={{ height: 400, width: '100%' }}>
    <DataGrid
      rows={rows}
      columns={columns}
      experimentalFeatures={{ newEditingApi: true }}
      density='compact'//行の幅
      autoHeight//行に合わせた高さ
      components={{
        Toolbar: GridToolbar,// ツールバーを指定する
      }}
      hideFooter//フッター非表示
      hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
      hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
      sx={StyleCommon}
      // getRowClassName={(params) =>  `super-app-theme--${params.row.Typ}`}
      getCellClassName={(params) => {
        let PF = params.field.split('_')[0]
        let n = params.field.split('_')[1]
        if (PF === 'Dmg' || PF === 'Ara' || PF === 'DmgBar') {// && params.value !== null　&& params.row.Elm1 === '炎'
          return params.row['Elm' + n]// return params.value >= 15 ? 'hot' : 'cold';
        }
      }}


    // initialState={{
    //   pagination: {
    //     paginationModel: {
    //       pageSize: 5,
    //     },
    //   },
    // }}
    // pageSizeOptions={[5]}
    // checkboxSelection
    // disableRowSelectionOnClick

    />
    // </Box>
  );
}

