import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import Button from '@mui/material/Button';

const UqOpGrt = {
  'HP+20': 'HP+25',
  '物理威力+6%': '物理威力+7%',
  '炎・氷・風属性威力+5%': '炎・氷・風属性威力+6%',
}



export default function DataGridBddyOpSlct(props) {
  const { name, OpNum, OpB, data, Op, Ctg, Calc, On, handleClose } = props.props


  const columns = [
    // { field: 'id', headerName: 'ID', width: 90 },
    {
      field: 'UqOp',
      headerName: '特性',
      width: 200,
    },
    {
      field: 'UqOpGrt',
      headerName: '特性(大成功)',
      width: 200,
      renderCell: (params) => (
        <Button
          // id={i}
          name={params.row.UqOpGrt}
          onClick={On}
        >
          {params.row.UqOpGrt}
        </Button>
      )
    },
    {
      field: 'Bddy',
      headerName: '所持モンスター',
      width: 1000,
    },

  ];


  const rows = [];

  Object.keys(data.BddyOp).forEach((OpName, i) => {
    // console.log(data.Bddy[BddyName].UqOp)
    rows.push({
      id: i,
      UqOp: OpName,
      UqOpGrt: data.BddyOp[OpName].Grt,
      Bddy:data.BddyOp[OpName].Bddy
    })
  })

  // Object.keys(UqOpGrt).forEach((OpName, i) => {
  //   // console.log(data.Bddy[BddyName].UqOp)
  //   rows.push({
  //     id: i,
  //     UqOp: OpName,
  //     UqOpGrt: UqOpGrt[OpName],
  //   })
  // })


  return (
    <Box sx={{ height: 600, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        components={{
          Toolbar: GridToolbar,// ツールバーを指定する
        }}
        hideFooter//フッター非表示
        hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
      // initialState={{
      //   pagination: {
      //     paginationModel: {
      //       pageSize: 5,
      //     },
      //   },
      // }}
      // pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      />
    </Box>
  );
}