import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import Button from '@mui/material/Button';

import ModalOpSlct from "./ModalOpSlct"
import CNSplit from "./CNSplit"
import { TextSrs } from './Text';



const WeArBdToTxt = { Wepn: '武器', Armr: '防具', Bddy: 'Buddy' }



export default function DataGridEq(props) {
  const { data, cNameF, Calc, Eq, handleClose, setEqs } = props.props

  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)

  const columns = [
    {
      field: 'name',
      headerName: '名称',
      width: 200,
      renderCell: (params) => (
        <Button
          name={params.row.name}
          // size="small"
          onClick={(event) => {
            data.C1[cNameF].Ally[Eq] = event.target.name
            setEqs(event.target.name)
            Calc()
            handleClose()
          }}
        >
          {params.row.name}
        </Button>
      )
    },
    {
      field: 'HP',
      headerName: 'HP',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: 'TP',
      headerName: 'TP',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: '攻撃力',
      headerName: '攻撃',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: '防御力',
      headerName: '防御',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: '速力',
      headerName: '速力',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可
    },
    {
      field: '精神力',
      headerName: '精神',
      align: 'right',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//メニュー不可
    },
    // {
    //   field: '移動力',
    //   headerName: '移動力',
    //   align: 'right',
    //   width: 50,
    // },
    {
      field: 'UqOp',
      headerName: '固有特性',
      width: 250,
    },
    {
      field: 'Op1',
      headerName: '特性(x3)',
      width: 250,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          Eq={params.row.Eq}
          OpB={params.row.Op1}
          data={data}
          Calc={Calc}
        />
      )

    },
    {
      field: 'Srs',
      headerName: 'シリーズ',
      width: 400,
    },

  ];


  const rows = [];
  Object.keys(data[Eq]).forEach((name, i) => {
    if (
      (Eq !== 'Armr') ||
      (Eq === 'Armr' && data.wearable[data.Chr[cName1][cName2].Typ][data[Eq][name].Knd])
    ) {
      rows.push({
        ...data[Eq][name].St,
        id: name,
        Eq: Eq,
        Ctg: WeArBdToTxt[Eq],
        name: name,
        UqOp: data[Eq][name].UqOp,
        Op1: data[Eq][name].Op,
        Srs: TextSrs({ name: name, data: data, Eq: Eq, }),
      })
    }
  })

  return (
    <Box sx={{ height: 700, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        components={{
          Toolbar: GridToolbar,// ツールバーを指定する
        }}
      // hideFooter//フッター非表示
      // hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
      // hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
      //   initialState={{
      //   pagination: {
      //     paginationModel: {
      //       pageSize: 10,
      //     },
      //   },
      // }}
      // pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      />
    </Box>
  );
}