import * as React from 'react';
import { styled } from '@mui/material/styles';
import Rating from '@mui/material/Rating';
import CheckBoxOutlineBlankIcon from '@mui/icons-material/CheckBoxOutlineBlank';
import CheckBoxIcon from '@mui/icons-material/CheckBox';
import KeyboardDoubleArrowUpIcon from '@mui/icons-material/KeyboardDoubleArrowUp';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import HorizontalRuleIcon from '@mui/icons-material/HorizontalRule';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';


const Bff = styled(Rating)({
  '& .MuiRating-iconFilled': {
    color: '#ff6666',
  },
  '& .MuiRating-iconHover': {
    color: '#ff0000',
  },
});
const Dbf = styled(Rating)({
  '& .MuiRating-iconFilled': {
    color: '#6666ff',
  },
  '& .MuiRating-iconHover': {
    color: '#0000ff',
  },
});


export default function RatingBff(props) {
  const { data, Calc, cNameF, id, V, EffName, EffEl, AE } = props.props
  // console.log(EffEl.Stk)
  const [value, setValue] = React.useState(EffEl.Stk ? EffEl.Stk : 0);

  let D = EffEl.FT ? true : false//FTが有れば編集不可
  

  return (
    <>
      {EffEl.Tgt === 'Enmy' ?
        <Dbf
          // name="テスト"
          // disabled={D}
          value={value}//defaultValueはNG
          max={EffEl.StkMax}
          icon={<CheckBoxIcon fontSize="inherit" />}
          emptyIcon={<CheckBoxOutlineBlankIcon fontSize="inherit" />}
          onChange={(event, newValue) => {
            setValue(newValue);
            EffEl.Stk = newValue

            let V = EffEl.V ? EffEl.V : ''
            if (newValue) {
              data.OthEff[EffName + '_' + EffEl.E + V] = newValue
            } else {
              delete data.OthEff[EffName + '_' + EffEl.E + V]
            }

            Calc()
          }}
        /> :
        <Bff
          // name="テスト"
          // disabled={D}
          value={value}//defaultValueはNG
          max={EffEl.StkMax}
          icon={<CheckBoxIcon fontSize="inherit" />}
          emptyIcon={<CheckBoxOutlineBlankIcon fontSize="inherit" />}
          onChange={(event, newValue) => {
            setValue(newValue);
            EffEl.Stk = newValue
            // console.log(newValue)
            // console.log(data.C1[cNameF][AE].Skl2)
            console.log(EffName)
            console.log(data.Skl)
            console.log(data.Ab)

            if (EffEl.All) {
              let V = EffEl.V ? EffEl.V : ''
              if (newValue) {
                data.OthEff[EffName + '_' + EffEl.E + V] = newValue
              } else {
                delete data.OthEff[EffName + '_' + EffEl.E + V]
              }

            }
            // console.log(EffEl)
            // console.log(data)

            Calc()
          }}
        />

      }
    </>
  );
}