import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Button from '@mui/material/Button';



export default function DataGridEnmy(props) {
  const { data, Calc, setSlEnmy } = props.props



  const On = (event) => {

    console.log('敵を' + event.target.name + 'に変更')

    data.SlEnmy = event.target.name
    setSlEnmy(event.target.name)

    Calc()
  };

  const columns = [
    // {
    //   field: 'No',
    //   headerName: '',
    //   width: 50,
    // },
    {
      field: 'Nam',
      headerName: '名称',
      width: 250,
      renderCell: (params) => (
        <Button
          name={params.row.NamQ}
          size="small"
          onClick={On}
        >
          {params.row.Nam}
        </Button>
      )
    },

    {
      field: 'Q',
      headerName: 'クエスト',
      width: 250,
    },

    {
      field: 'Sz',
      headerName: 'サイズ',
      width: 60,
    },
  ];



  const rows = Object.keys(data.Enmy).map((EnmyNam, i) => {
    let Q = EnmyNam.includes('(') ? EnmyNam.split('(')[1].split(')')[0] : ''
    

    return ({
      ...data.Enmy[EnmyNam],
      id: i,
      NamQ: EnmyNam,
      Nam: EnmyNam.split('(')[0],
      Q: Q,

    })
  })




  return (
    <Box sx={{ height: 1500, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//行の幅
        // autoHeight//行に合わせた高さ
        hideFooter//フッター非表示
        hideFooterSelectedRowCount//フッターに表示されている選択中の行数を非表示
        hideFooterPagination//フッターに表示されているページ当たりの行数、ページングを非表示にする。(無料版のみ）
        getRowHeight={() => 'auto'}
      />
    </Box>
  );
}