import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';

import DataGridSt from "./DataGridSt";
import DataGridBff from "./DataGridBff";


import { GridAmp, GridAdv, GridRs, GridCut } from "./GridParams";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Box
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 1 }}>
          {children}
        </Box>
      )}
    </Box>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function TabParams(props) {
  const { data, C, Calc, list } = props
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="ステータス" {...a11yProps(0)} />
          <Tab label="バフ・デバフ" {...a11yProps(1)} />
          <Tab label="威力" {...a11yProps(2)} />
          <Tab label="逆境・盤石" {...a11yProps(3)} />
          <Tab label="耐性" {...a11yProps(4)} />
          <Tab label="被ダメ増加・軽減" {...a11yProps(5)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <DataGridSt props={props.props} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <DataGridBff props={{ ...props.props }} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <GridAmp props={{ ...props.props }} />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <GridAdv props={{ ...props.props }} />
      </TabPanel>
      <TabPanel value={value} index={4}>
        <GridRs props={{ ...props.props}} />
      </TabPanel>
      <TabPanel value={value} index={5}>
        <GridCut props={{ ...props.props}} />
      </TabPanel>
    </Box>
  );
}