import * as React from 'react';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';


import DataGridDmgChr from "./DataGridDmgChr";
import DataGridChrEq from "./DataGridChrEq";
import TabAE from "./TabAE";
import RatingAwk from "./RatingAwk";
import ModalEqSlct from "./ModalEqSlct";
import {ImgIcon} from './Img';
import CNSplit from "./CNSplit"
import ModalChrSkl from './ModalChrSkl';
import { TextCNameF } from './Text';

export default function GridChr(props) {
  const { data, Calc, cNameF } = props.props

  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)

  return (
    <Grid container spacing={1}>
      <Grid xs={6}>
        <Grid container spacing={0}>
          <Grid xs='auto' sx={{ pr: 1 }}>
            <ImgIcon name={cNameF + 'i'} width={200} />
          </Grid>
          <Grid xs>
            <TextCNameF text={cNameF} />
            <ImgIcon name={data.Chr[cName1][cName2].Gr} width={35} /><ImgIcon name={data.Chr[cName1][cName2].Typ} width={35} /><br />
            Lv100　EXLv45　<RatingAwk props={props.props} /><br />
            {/* <Grid container spacing={1}> */}
            <ModalChrSkl props={{ ...props.props, AE: 'Ally' }} />

          </Grid>
          <Grid xs={12}>
            <br/>
            <DataGridChrEq props={{ ...props.props, AE: 'Ally' }}/>

            {/* クリティカル率+{data.C1[cNameF].Ally.Crr}<br />
            被ダメージ軽減{data.C1[cNameF].Ally.Cut.軽減}<br />
            被ダメージ増加{data.C1[cNameF].Ally.Cut.増加} */}
            {/* <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: 'Wepn' }} />
            <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: 'Armr' }}/>
            <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: 'Bddy' }} /> */}
            {/* </Grid> */}
          </Grid>
        </Grid>
      </Grid>
      <Grid xs={6}>
        <TabAE props={props.props} />
      </Grid>
      <Grid xs={6}>
        <DataGridDmgChr props={{ ...props.props, AE: 'Ally' }} />
      </Grid>
      <Grid xs={6}>
        <DataGridDmgChr props={{ ...props.props, AE: 'Enmy' }} />
      </Grid>
    </Grid>
  );
}