import * as React from "react";
import Rating from "@mui/material/Rating";
import Typography from '@mui/material/Typography';
import FilterVintageIcon from '@mui/icons-material/FilterVintage';

export default function RatingAwk(props) {
  const { data, Calc, list, cNameF } = props.props

  const [value, setValue] = React.useState(data.C1[cNameF].Ally.Awk);

  return (
    <Rating
      // size="small"
      name="覚醒"
      value={value}
      icon={<FilterVintageIcon fontSize="inherit" />}
      emptyIcon={<FilterVintageIcon fontSize="inherit" />}
      onChange={(event, newValue) => {
        data.C1[cNameF].Ally.Awk = newValue
        setValue(newValue);
        Calc()
      }}
    />
  );
}