import * as React from 'react';

import EffAdd from "./EffAdd"
import CNSplit from "./CNSplit"


// const CalcSt = (props) => {
export default function CalcSt(props) {
  const { data, cNameF, isIncludes, AE, round } = props


  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)

  let EqObj = {
    Wepn: data.C1[cNameF].Ally.Wepn,
    Armr: data.C1[cNameF].Ally.Armr,
    Bddy: data.C1[cNameF].Ally.Bddy
  }

  let Gr = data.Chr[cName1][cName2].Gr
  let t = data.t

  const MRnkEff = (MRnk, Gr, S) => {
    return Math.ceil((MRnk[Gr] - data.list.HDSAIT.indexOf(S)) / 6)
  }

  const OpEffAdd = (OpNam, AddName) => {
    if (isIncludes(data.list.HTADSIM, OpNam)) {//特性がステータス加算なら

      data.C1[cNameF][AE].St.Org[OpNam.split('+')[0]] += Number(OpNam.split('+')[1])

    } else if (OpNam.includes('威力')) {//特性が威力+なら

      data.C1[cNameF][AE].Eff0.push({
        N: AddName,
        E: OpNam.split('+')[0] + '+',
        V: OpNam.split('+')[1]
      })

    } else if (isIncludes(data.list.耐性, OpNam)) {//耐性なら
      if (OpNam.includes('・')) {//複数効果なら
        let V = Number(OpNam.split('耐性+')[1].split('%')[0])
        let OpNamList = OpNam.split('耐性+')[0].split('・')
        OpNamList.forEach((OpNam2) => {
          let OpNam3 = OpNam2
          if (!OpNam2.includes('属性')) OpNam3 += '属性'
          data.C1[cNameF].Ally.耐性[OpNam3] += V
        })
      }

      data.C1[cNameF].Ally.耐性[OpNam.split('耐性+')[0]] += Number(OpNam.split('耐性+')[1].split('%')[0])
    }

  }//OpEffAdd




  if (AE === 'Ally') {//装備無ステータス,Allyのみ
    data.list.HTADSIM.forEach((S, i) => {//各ステータスforEach

      data.C1[cNameF][AE].St.Bas[S] = data.Chr[cName1][cName2].St.Bas[S]//素ステ

      if (data.Chr[cName1][cName2].St.ExP[S]) {//Exパッシブがあれば加算
        data.C1[cNameF][AE].St.Bas[S] += data.Chr[cName1][cName2].St.ExP[S]
      }

      Object.keys(data.Chr[cName1]).forEach((cName2) => {//同名キャラforEach
        if (data.Chr[cName1][cName2].St.UqP[S]) {//固有パッシブがあれば加算
          data.C1[cNameF][AE].St.Bas[S] += data.Chr[cName1][cName2].St.UqP[S]
        }
      })

      if (S !== '移動力') {//乗算
        data.C1[cNameF][AE].St.Bas[S] *= (
          100 +
          data.Chr[cName1][cName2].St.ExB[S] +//*Exボーナス
          data.C1[cNameF][AE].Awk * 5 +//覚醒
          MRnkEff(data.MRnk, data.C1[cNameF][AE].Gr, S)//マスターランク
        ) / 100
      }

      data.C1[cNameF][AE].St.Bas[S] = round(data.C1[cNameF][AE].St.Bas[S])//四捨五入の5を捨て
    })
  }//装備無ステータス


  //装備有りステータス
  data.list.HTADSIM.forEach((S) => {//まず
    data.C1[cNameF].Ally.St.Org[S] = data.C1[cNameF].Ally.St.Bas[S]
  })

  Object.keys(EqObj).forEach((Eq) => {//武器防具Buddy_forEach
    let EqNam = EqObj[Eq]

    Object.keys(data[Eq][EqNam].St).forEach((S) => {//各ステ加算
      data.C1[cNameF].Ally.St.Org[S] += data[Eq][EqNam].St[S]
    })

    OpEffAdd(data[Eq][EqNam].UqOp, EqNam)
    OpEffAdd(data[Eq][EqNam].Op, EqNam)
    OpEffAdd(data[Eq][EqNam].Op, EqNam)
    OpEffAdd(data[Eq][EqNam].Op, EqNam)

    if (data[Eq][EqNam].Srs) {//シリーズが設定されているなら
      if (data.C1[cNameF].Ally.Srs[data[Eq][EqNam].Srs]) {//枠があれば
        data.C1[cNameF].Ally.Srs[data[Eq][EqNam].Srs] += 1//そのシリーズ+1
      } else {//枠がないなら作成
        data.C1[cNameF].Ally.Srs[data[Eq][EqNam].Srs] = 1
      }
    }
  })//武器防具Buddy_forEach

  Object.keys(data.C1[cNameF].Ally.Srs).forEach((SrsNam) => {//シリーズ効果があるならforEach
    if (data.C1[cNameF].Ally.Srs[SrsNam] >= 2) {//2個以上なら
      data.Srs[SrsNam + '1'].SrsEffList.forEach((OpNam) => {
        OpEffAdd(OpNam, SrsNam)
      })
      if (data.C1[cNameF].Ally.Srs[SrsNam] >= 3 && data.Srs[SrsNam + '2']) {//更に3個以上で、効果2があるなら
        data.Srs[SrsNam + '2'].SrsEffList.forEach((OpNam) => {
          OpEffAdd(OpNam, SrsNam)
        })
      }
    }
  })//シリーズ効果があるならforEach

  // Object.keys(data.Chr[cName1]).forEach((cName2) => {//同名キャラforEach
  //   if (data.Chr[cName1][cName2].UqP) {//効果タイプの固有パッシブがあれば
  //     data.Chr[cName1][cName2].UqP.forEach((AbName) => {//固有パッシブforEach

  //       // if (cNameF === '【ひと夏の思い出作り】ソフィア') {
  //       //   console.log(AbName)
  //       // }

  //       EffAdd({
  //         data: data,
  //         cNameF: cNameF,
  //         AbName: AbName,
  //         isIncludes: isIncludes,
  //         AE: AE,
  //       })
  //     })
  //   }
  // })

  data.C1[cNameF].Ally.AbAll.forEach((AbName) => {

    // if(cNameF==='【ナース姿の先生】アリスカ'){
    //   console.log(AbName)
    // }
  

    EffAdd({
      data: data,
      cNameF: cNameF,
      AbName: AbName,
      isIncludes: isIncludes,
      AE: AE,
    })
  })

}

