import * as React from 'react';

import Button from '@mui/material/Button';

export default function ButtonBffClear(props) {
  const { data, Calc } = props.props
  return (
    <Button
      variant="outlined"
      color="error"
      fullWidth
      onClick={() => {
        let SklAb = ['Skl', 'Ab']
        SklAb.forEach((SA) => {
          Object.keys(data[SA]).forEach((name) => {
            if (data[SA][name].Eff) {
              data[SA][name].Eff.forEach((EffEl) => {
                EffEl.Stk = 0
              })
            }
          })
        })
        Calc()
      }}
    >
      全てのバフ・デバフをクリア
    </Button>
  );
}