import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Modal from '@mui/material/Modal';
import Grid from '@mui/material/Unstable_Grid2';

import DataGridBddyOpSlct from "./DataGridBddyOpSlct"

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 1000,
  bgcolor: '#fffef2',
  border: '2px solid #000',
  boxShadow: 24,
  p: 2,
};

export default function ModalOpSlct(props) {
  const { name, OpB, data, Calc, Eq } = props
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [OpA, setOpA] = React.useState(OpB);

  const On = (event) => {
    let OpBtest = data[Eq][name].Op
    data[Eq][name].Op = event.target.name
    setOpA(event.target.name)
    console.log(name + 'のOpを' + OpBtest + 'から' + event.target.name + 'に変更')
    Calc()
    handleClose()
  };

  return (
    <div>
      <Button onClick={handleOpen} size="small">{OpA}</Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          {Eq === 'Bddy' ?
            <DataGridBddyOpSlct
              props={{
                ...props,
                On: On,
                handleClose: handleClose
              }}
            /> :
            <Grid container spacing={0}>
              {data[Eq][name].OpList.map((OpName, i) => {
                return (
                  <Grid xs={4} key={i} sx={{p:0.25}}>
                    <Button
                      id={i}
                      name={OpName}
                      size="large"
                      variant="outlined"
                      onClick={On}
                      fullWidth
                    >
                      {OpName}
                    </Button>
                  </Grid>
                )
              })}
            </Grid>

          }



        </Box>
      </Modal>
    </div>
  );
}