import * as React from 'react';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

export default function SwOthEff(props) {
  const { data, Calc } = props.props


  return (
    <FormControlLabel
      control={
        <Switch
          props={props.props}
          defaultChecked
          onChange={(event, TorF) => {
            data.OthEffOn = TorF
            console.log(TorF)
            Calc()
          }}
        />
      }
      label="スキルや特性による「自分以外のなかまにもかかるバフ」「敵へのデバフ」を全キャラに適用　※リーダースキルは全キャラ適用で固定"
    />


  );
}