import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';


import GridChr from "./GridChr";
import GridChrSkl from "./GridChrSkl";
import DetailSkill from "./DetailSkill";
import { alignProperty } from '@mui/material/styles/cssUtils';

import {ImgIcon} from './Img';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 1500,

  // bgcolor: 'background.paper',
  bgcolor: '#fffef2',

  border: '2px solid #000',
  boxShadow: 24,
  p: 2,
};

export default function ModalCommon(props) {
  const { data, Calc, list, cNameF, id, V } = props.props

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <>
      <Button onClick={handleOpen} sx={{ align: "right" }}>
        <ImgIcon name={cNameF + 'i'} width={80} />


      </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <GridChr props={props.props} />

        </Box>
      </Modal>
    </>
  );
}