export function ImgIcon(props) {
  const { name, width, height } = props
  return (
    <img
      src={`${process.env.PUBLIC_URL}/${name}.png`}
      style={{ width: width, height: height, borderRadius: 2 }}
      alt={''}
    />
  )
}

export function ImgDmg(props) {
  const { data, Dmg, HP, AE } = props
  if (AE === 'Ally') {
    return (
      <>
        {Dmg ?
          <img
            src={`${process.env.PUBLIC_URL}/DmgBar.png`}
            style={{ width: Dmg / (HP / 100) * data.scale, height: 100 }}
            alt={''}
          /> : ''
        }
      </>
    )
  } else {
    return (
      <>
        {HP ?
          <img
            src={`${process.env.PUBLIC_URL}/HPBar.png`}
            style={{ width: Math.max((HP - Dmg), 0) / 20, height: 100 }}
            alt={''}
          /> : ''
        }
        {Dmg ?
          <img
            src={`${process.env.PUBLIC_URL}/DmgBar.png`}
            style={{ width: Math.min(Dmg,HP) / 20, height: 100 }}
            alt={''}
          /> : ''
        }
      </>

    )

  }
}

