import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Unstable_Grid2';

import DataGridDmg from './DataGridDmg'
import InputZHP from './InputZHP'
import InputT from './InputT'


import DataGridSkl from './DataGridSkl'
import DataGridEq from './DataGridEq'
import GridMRnk from './GridMRnk'

import InputEnmy from './InputEnmy'
import DataGridEnmy from './DataGridEnmy'
import SwOthEff from './SwOthEff'
import ButtonBffClear from './ButtonBffClear'
import SliderDmg from './SliderDmg'

function TabPanel(props) {
  const { children, value, index, ...other } = props;


  return (
    <Box
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 1 }}>
          {children}
        </Box>
      )}
    </Box>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function TabTop(props) {
  const { data, C, Calc, list } = props.props

  const [value, setValue] = React.useState(0);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const [LS, setLS] = React.useState(data.LS);
  const [SlEnmy, setSlEnmy] = React.useState(data.SlEnmy);


  return (
    <Box sx={{ width: '100%' }}>
      <Grid container spacing={0} columns={16}>

        {/* <Grid xs={1} sx={{ px: 1 }}>
          <InputT props={{ ...props.props }} />
        </Grid> */}
        <Grid xs='auto' sx={{ px: 1 }}>
          <InputZHP props={props.props} C={C} Calc={Calc} />
        </Grid>

        <Grid xs={3} sx={{ pr: 1 }}>
          <TextField
            disabled
            id="LS"
            label="リーダースキル"
            value={LS}
            size='small'
            fullWidth
          />
        </Grid>
        <Grid xs={4}>
          <TextField
            disabled
            id="SlEnmy"
            label="敵"
            value={SlEnmy}
            size='small'
            fullWidth
          />
        </Grid>
        <Grid xs={16} sx={{ pl: 1 }}>
          
        </Grid>
        <Grid xs={16} sx={{ pl: 1 }}>
          <SwOthEff props={props.props} />
        </Grid>
      </Grid>

      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
          <Tab label="キャラ" {...a11yProps(0)} />
          <Tab label="スキル・特性" {...a11yProps(1)} />
          <Tab label="武器・防具・Buddy" {...a11yProps(2)} />
          <Tab label="マスターランク" {...a11yProps(3)} />
          <Tab label="敵" {...a11yProps(4)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <Grid container spacing={0}>
          <Grid xs={10}>
          <SliderDmg  props={props.props}/>
          </Grid>
          <Grid xs={2} sx={{ pb: 0.5 }}>
            <ButtonBffClear props={props.props} />
          </Grid>
        </Grid>

        <DataGridDmg props={{ ...props.props, setLS: setLS }} C={props.C} />
      </TabPanel>
      <TabPanel value={value} index={1}>


        <DataGridSkl props={props.props} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <DataGridEq props={{ ...props.props }} />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <GridMRnk props={{ ...props.props }} />
      </TabPanel>
      <TabPanel value={value} index={4}>
        <InputEnmy props={{ ...props.props, setSlEnmy: setSlEnmy }} />
        <DataGridEnmy props={{ ...props.props, setSlEnmy: setSlEnmy }} />
      </TabPanel>

    </Box>
  );
}